@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*,*::after,*::before{padding:0;margin:0;box-sizing:border-box}img{max-width:100%}button{outline:none;border:none;cursor:pointer;border-radius:10px}button:hover{opacity:.8}body{font-family:'Montserrat', sans-serif;background:#FF512F;background:linear-gradient(to right, #F09819, #FF512F)}body main{text-align:center}header{padding:1em}header h1{text-align:center;margin:.2em 0}header h1:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.particles{position:fixed;top:0;left:0;bottom:0;right:0;z-index:-1}

.sub__def{display:flex}@media (max-width: 600px){.sub__def{flex-direction:column}}@media (max-width: 600px){.sub__def .sub__detail{width:100%;text-align:center;padding:0 1em .5em}}.sub__def .sub__img{width:100px}@media (max-width: 600px){.sub__def .sub__img{width:30%}}

